import React from "react";
import { useState } from "react";
import "./BMI.css"

export default function BMI() {
  const [weight, setWeight] = useState("");
  const [height, setHeight] = useState("");
  const [bmi, setBmi] = useState("");
  const [message, setMessage] = useState("");

  const calculateBmi = (e) => {
    e.preventDefault();
    if (weight === 0 || height === 0) {
      alert("invalid input ");
    } else {
      let bmi = weight / (height / 100) ** 2;
      setBmi(bmi.toFixed(2));

      if (bmi < 18.5) {
        setMessage("You are underwight");
      } else if (bmi >= 18.5 && bmi <= 24.9) {
        setMessage("You are healthy");
      } else if (bmi >= 24.9 && bmi <= 29.9) {
        setMessage("You are over Weight");
      } else if (bmi >= 29.8 && bmi <= 100) {
        setMessage("You are more more more Obese...");
      } else {
        setMessage("Kindly Enter weight and height");
      }
    }
  };

  let imgsrc;

  if (bmi < 1) {
    imgsrc = null;
  } else {
    if (bmi < 18.5) {
      imgsrc = require("../../src/assets/underweight.png");
    } else if (bmi >= 18.5 && bmi <= 24.9) {
      imgsrc = require("../../src/assets/healthy.png");
    } else if (bmi >= 24.9 && bmi <= 29.9) {
      imgsrc = require("../../src/assets/overweight.png");
    } else if (bmi >= 29.9 && bmi <= 100) {
      imgsrc = require("../../src/assets/obese.png");
    } else {
      imgsrc = require("../../src/assets/oops.png");
    }
  }

  const imaging = {
    display: "block",
    margin: "auto",
  };

  return (
    <>
      <div className="container mt-5">
        <div className="row">
                <h2 className="text-center my-5">BMI Calculator</h2>
          <div className="col-md-4">
            <div className="bmi-container">
              <form onClick={calculateBmi}>
                <div className="bmi-iiner mt-5">
                  <div className="first-input mt-5">
                    <label htmlFor="weight">Weight(kg)</label>
                    <input
                      type="text"
                      placeholder="Weight(in kg)"
                      required
                      className="form-control"
                      value={weight}
                      onChange={(e) => setWeight(e.target.value)}
                    ></input>
                  </div>
                  <div className="second-input mt-3">
                    <label htmlFor="height">height(cm)</label>
                    <input
                      type="text"
                      placeholder="height in (cm)"
                      required
                      className="form-control"
                      value={height}
                      onChange={(e) => setHeight(e.target.value)}
                    ></input>
                  </div>
                  <button className="btn btn-primary d-block m-auto mt-4">
                    Submit
                  </button>
                </div>
              </form>
            </div>
          </div>
          <div className="col-md-8">
            <div className="center text-center">
              <p>
                Your BMI is<strong> {bmi}</strong>
              </p>
              <p>
                {" "}
                <strong>{message}</strong>
              </p>
            </div>
            <div className="imgfat">
              <img src={imgsrc} style={imaging} alt="motapaimage"></img>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
