import React,{useState} from 'react'
import Picker from 'emoji-picker-react';
import "./Emoji.css"

const Emoji = () => {
  const [chooseemohi, setChosenEmoji] = useState(null);

  const onEmojiClick = (event, emojiObject) => {
    setChosenEmoji(emojiObject);
  };
  return (
    <div className='emojicontainer'>
    <div className="emojiinner">
     {chooseemohi ?(
<div className='choose'>You choose: {chooseemohi.emoji}</div>
     ):(
      <div className='noemoji'>No emoji choosen</div>
     )}

     <Picker onEmojiClick={onEmojiClick}/>
     </div>
    </div>
  )
}

export default Emoji
