import React from "react";
import { useState } from "react";
import "./Counter.css"

export default function Counter(){

    const [count,setCount] = useState(0)

    const increase =()=>{
        setCount(count+1);
        console.log(count);
    }

    const decrease = ()=>{
        setCount(prevCount=>{
            if(prevCount<=0) return 0;
            return prevCount-1;
        })
    }

    const reset = ()=>{
        setCount(0);
    }


    

    return(
        <>
                <div className="App">
                <h1 className="my-5 text-center">Counter</h1>
      <div className="counter-container">
      <div className="counter">
      <p className="count">{count}</p>
      <div className="controls">
        <button className="btn btn-primary mx-2" onClick={increase}>Increase</button>
        <button className="btn btn-primary mx-2" onClick={decrease}>Decrease</button>
        <button className="btn btn-primary mx-2" onClick={reset}>Reset</button>
      </div>
    </div>
 
      </div>
    </div>

    <h1 className="text-center mt-5 fw-bold">{new Date().toLocaleTimeString()}</h1>

        </>
    )
}